import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { menumodel } from '../core/Models/menumodel';
@Component({
  selector: 'app-banks-list',
  templateUrl: './banks-list.component.html',
  styleUrls: ['./banks-list.component.css']
})
export class BanksListComponent implements OnInit {
  entries: menumodel[] = [];
  constructor(private router: Router) { }
  MenuHandler(m: menumodel)
  {
    const NavArray = [];
    NavArray.push(m.route);
    this.router.navigate(NavArray);
  }
  ngOnInit(): void {
    let m = new menumodel();
    m.fa_icon = 'fa fa-bank';
    m.title = 'Dalc Bank';
    m.route = 'wrapper/branch-list';
    this.entries.push(m);

    m = new menumodel();
    m.fa_icon = 'fa fa-bank';
    m.title = 'Db Bank';
    m.route = 'wrapper/branch-list';
    this.entries.push(m);
    
    m = new menumodel();
    m.fa_icon = 'fa fa-bank';
    m.title = 'Front Bank';
    m.route = 'wrapper/branch-list';
    this.entries.push(m);
    
    m = new menumodel();
    m.fa_icon = 'fa fa-bank';
    m.title = ' Back Bank';
    m.route = 'wrapper/branch-list';
    this.entries.push(m);
    
 
  }

}
